const state = {
    tagList:[ //tagListnamenametagContent
            {id:0, name:'canyin', tagContent: '', type: '-'},
            {id:1, name:'shuidianmei', tagContent: '',type: '-'},
            {id:2, name:'tongxun', tagContent: '', type: '-'},
            {id:3, name:'xuexi', tagContent: '', type: '-'},
            {id:4, name:'yiliao', tagContent: '', type: '-'},
            {id:5, name:'yule', tagContent: '', type: '-'},
            {id:6, name:'zhufang', tagContent: '', type: '-'},
            {id:7, name:'shejiao', tagContent: '', type: '-'},
            {id:8, name:'lingshi', tagContent: '', type: '-'},
            {id:9, name:'jujia', tagContent: '', type: '-'},
            {id:10, name:'jiaotong', tagContent: '', type: '-'},
            {id:11, name:'gouwu', tagContent: '', type: '-'},           
            {id:12, name:'fushi', tagContent: '', type: '-'},  
            {id:13, name:'qitashouru', tagContent: '', type: '+'},
            {id:14, name:'licaishouyi', tagContent: '', type: '+'},
            {id:15, name:'gongzi', tagContent: '', type: '+'},
            {id:16, name:'jiekuan', tagContent: '', type: '+'},
            {id:17, name:'jixiaojiangjin', tagContent: '', type: '+'},
            {id:18, name:'ziyuan', tagContent: '', type: '+'},          
    ],
    currentTag:{},
    iconCost:Object.freeze([ //
        {name:'canyin', tagContent: ''},
        {name:'shuidianmei', tagContent: ''},
        {name:'tongxun', tagContent: ''},
        {name:'xuexi', tagContent: ''},
        {name:'yiliao', tagContent: ''},
        {name:'yule', tagContent: ''},
        {name:'zhufang', tagContent: ''},
        {name:'shejiao', tagContent: ''},
        {name:'lingshi', tagContent: ''},
        {name:'jujia', tagContent: ''},
        {name:'jiaotong', tagContent: ''},
        {name:'gouwu', tagContent: ''},           
        {name:'fushi', tagContent: ''},    
    ]),
    iconEarn:Object.freeze([
        {name:'qitashouru', tagContent: ''},
        {name:'licaishouyi', tagContent: ''},
        {name:'gongzi', tagContent: ''},
        {name:'jiekuan', tagContent: ''},
        {name:'jixiaojiangjin', tagContent: ''},
        {name:'ziyuan', tagContent: ''},
    ]),
} as TagState;
const mutations = {  
    fetchTags(state:TagState):void{
        const tagList2 = window.localStorage.getItem('tagList');
        //tagList2null 
        if(tagList2){ state.tagList = JSON.parse(tagList2)}   
    },
    setCurrentTag(state:TagState,payload:TagD):void{
        state.currentTag = payload;
        window.localStorage.setItem('currentTag',JSON.stringify(state.currentTag));
    },
    fetchCurrentTag(state:TagState):void{
        const currentTag2 = window.localStorage.getItem('currentTag');
        if(currentTag2){ state.currentTag = JSON.parse(currentTag2)}
    },
    //
    updateTag(state:TagState, payload:{name:string, tagContent:string}):void{
        const tagTextList = state.tagList.map((item: TagD) => item.tagContent);
        //
        const id2 = state.currentTag.id;        
        if(payload.tagContent){            
            if(tagTextList.indexOf(payload.tagContent) >= 0 && state.tagList[id2].tagContent!== payload.tagContent){ 
                alert('')
            }else{
                state.tagList[id2].tagContent = payload.tagContent;
                state.tagList[id2].name = payload.name;
                store.commit('saveTags');
            }                  
        }else{
            alert('')
        }
    },
    //
    createNewTag(state:TagState, payload:{type:string, name:string, tagContent:string}):void{
        store.commit('fetchTags')
        if(payload.tagContent===''){ alert('');return;}
        const textTagList = state.tagList.map((item: TagD) => item.tagContent);       
        if(payload.type==='-' || payload.type==='+'){
            if(textTagList.indexOf(payload.tagContent) >= 0){
                alert('')
            }else{
                const maxTagId = creTagId();
                state.tagList
                    .push({id:maxTagId,name:payload.name, tagContent:payload.tagContent, type:payload.type});
                store.commit('saveTags');
            }
        }else{
            alert('')
        }
    },
    //
    saveTags(state:TagState):void{
        window.localStorage.setItem('tagList', JSON.stringify(state.tagList));    
    },
    //
    // deleteTag(state:TagState):void{
    //     const deId = state.currentTag.id;
    //     state.tagList.splice(deId, 1);
    //     for(let i=0; i < state.tagList.length; i++){
    //         state.tagList[i].id = i;
    //     }
    //     state.currentTag = {} as TagD;
    //     store.commit('saveTags');
    // }

}

export default {
    state,
    mutations,
    actions: {
    },
    modules: {
    }
}
  