






import Vue from 'vue';  
import * as echarts from 'echarts'; //import echarts from... init undefined
import { EChartsOption } from 'echarts';
import {Component, Prop, Watch} from 'vue-property-decorator';
@Component
export default class Echarts extends Vue{
    @Prop()readonly option?: EChartsOption; 
    mychart!:echarts.ECharts ;
    mounted(){  
        const div = (this.$refs.chartWrapper as HTMLElement);
        div.scrollLeft = div.scrollWidth; // 
        if(!this.option){
            return console.error('option')
        }else{   
            // as HTMLDivElementvueEChartsinitHTML
            this.mychart = echarts.init(this.$refs.container as HTMLDivElement)
            this.mychart.setOption(this.option)
        }
    }
    @Watch('option')
    upChart(newval:EChartsOption){
        this.mychart.setOption(newval)
    }

}
